	module.exports = {"title":"Craft as in Beer","date":"2014-03-28T22:12:03.284Z","category":"article","teaser":"In a sea of news, trends, frameworks, and other noise, it's important to focus on our craft.","body":"<p class='Mb(r1)'>Last night I spoke at the excellent &quot;<a href='http://www.webdirections.org/events/wdyk-brisbane-mar2014/'\n    \n    class='Link'>What Do You Know</a>&quot; event, run by <a href='http://www.webdirections.org'\n    \n    class='Link'>Web Directions</a>. Since <a href='/presentations/craft-as-in-beer'\n    \n    class='Link'>my slides</a> are not exactly self explanatory, I thought I would post this alongside them to help those who missed the event.</p><hr>\n<p class='Mb(r1)'>I love a good beer. But not just any beer. When you taste one of these exceptional brews, you don&#39;t skull it, you savour it. For me this is usually in the form of <strong>craft</strong> beer.</p><p class='Mb(r1)'>According to the American Brewers Association craft beer is practically defined by:</p><ul class=\"Mb(r1) List(s)\">\n<li>Small</li>\n<li>Independent</li>\n<li>Traditional</li>\n</ul>\n<p class='Mb(r1)'>I would argue that great craft breweries are actually defined by:</p><ul class=\"Mb(r1) List(s)\">\n<li>Experience</li>\n<li>Quality</li>\n<li>Passion</li>\n</ul>\n<p class='Mb(r1)'>This second definition is harder to use in practice, but it&#39;s one that lends itself to the reason I love craft beer.</p><p class='Mb(r1)'>The other reason I like the second definition, is that it not only defines craft beer, but any craftsperson. So, when thinking of the role craftspersonship and the web, I naturally thought of sushi.</p><p class='Mb(r1)'>OK, maybe that&#39;s not natural. But to be specific I was thinking of Jiro Ono, arguably the worlds best sushi chef. As documented in the movie “Jiro Dreams of Sushi”, Jiro has been honing his craft for over 75 years. He cares about every single step in the life of one of his dishes, all the way back to the inception of his ingredients. It’s this care that allows him to create one of the most exceptional dining experiences available.</p><p class='Mb(r1)'>Bringing it back to the web, and how we design and develop for multiple devices, it constantly strikes me that many don’t take this care in their work.</p><p class='Mb(r1)'>So what can we learn from Jiro? The main thing that struck me about Jiro was his relentless pursuit of perfection. In this pursuit I learnt 3 things that help in design and development on the web.</p><h2 id=\"master-the-simple-things\" class='Fz(ms2) Fw(800) Lh(1) Mt(r2) Mb(r1)'>\n      <a href=\"#master-the-simple-things\" class='Cur(d)!'>Master the Simple Things</a>\n    </h2><p class='Mb(r1)'>One of the first things you notice about the sushi Jiro creates, is that it is simple, not simplistic, but simple. Each plate of sushi looks simple, but the attention to detail that goes into that simple plate is astonishing. From ageing tuna for 3-10 days depending on its age, to massaging octopus for 50 minutes. To a customer, this may look look like a simple piece of tuna or octopus, but after tasting it, the quality, experience and passion put into it is evident.</p><p class='Mb(r1)'>It is this mastery of the simple things that we can bring to design and development. For instance do you know what attributes you should be using in your viewport meta tag? Do you know how to keep the aspect ratio of your video when it&#39;s container adapts responsively? These simple solutions can fix large usability problems I have seen again and again. Too often I see developers skipping over this small, simple stuff and coming up with complex solutions to responsive design that effectively breaks on a large percentage of devices.</p><p class='Mb(r1)'>I don&#39;t know about you, but because of the rate of change on the web and the team members I need to collaborate with, trying to keep this knowledge all in my head is not going to work as well for me as it does for Jiro. One way I deal with this is to keep as much of my process documented as possible. I have my own <a href='https://github.com/lukebrooker/CSS-Guidelines'\n    \n    class='Link'>CSS guidelines</a>, <a href='https://github.com/lukebrooker/spark'\n    \n    class='Link'>project boilerplate</a> and I keep a library of UX research and best practices that I keep updating and referencing.</p><p class='Mb(r1)'>By doing this, I keep a strong foundation. When new techniques are discovered or bought to my attention I can adapt my thinking, add it to my guidelines and still have a strong foundation to base future decisions on.</p><h2 id=\"understand-your-dependencies\" class='Fz(ms2) Fw(800) Lh(1) Mt(r2) Mb(r1)'>\n      <a href=\"#understand-your-dependencies\" class='Cur(d)!'>Understand your Dependencies</a>\n    </h2><p class='Mb(r1)'>As much as a perfectionist as Jiro is, he can&#39;t control everything in his dishes. If he was out growing and catching his ingredients he wouldn&#39;t have time to prepare the dishes he is so famous for. But he doesn&#39;t just go to the local supermarket and grab some cans of tuna. He has formed relationships with vendors at the local markets and understands every detail about the ingredients he is using.</p><p class='Mb(r1)'>Sometimes as web developers, we don&#39;t have the luxury to create everything from scratch or it just doesn&#39;t make sense for us to build a complex JavaScript library that someone else has already spent years of development and testing creating. But this doesn&#39;t mean we should blindly add these things to our projects.</p><p class='Mb(r1)'>If we want to ensure the quality and maintainability of our work, we need to understand each dependency we include. How deep this knowledge goes, depends on the potential impact of the dependency. A small, flexible library that can easily be replaced, may not need to be investigated too deeply, but a large framework that will become the foundation for your whole UI, is something you should take very seriously.</p><p class='Mb(r1)'>The biggest offender to this of late is Bootstrap. Not that Bootstrap itself doing something specifically wrong for what it is trying to achieve, but that developers <strong>blindly</strong> include it as a foundation for major projects is astonishing to me. Using Bootstrap usually end up with you adding it somewhere in your project and then override it until you get what you want and end up with convoluted CSS file that nobody is sure what is going so every new property gets added to the bottom with extra specificity and the eventual !IMPORTANT (typed as loudly as possible).</p><p class='Mb(r1)'>Besides not being maintainable, the design and development solutions included in a framework like this aren&#39;t always the best for your particular problem… but you treat it like a nail and use one of the 20 or so hammers your were given.</p><p class='Mb(r1)'>Don&#39;t get me wrong, I love that these frameworks are out there. As soon as a new version is out I will scour the code to see what they are doing differently. If I like something I see, I can add it back to my boilerplate or adapt my guidelines. If you want to use a framework like this, go ahead. But for me it seem like giving this control to a framework is like a brewery making beer from a home brew kit.</p><h2 id=\"extend-simple-with-creative-thinking\" class='Fz(ms2) Fw(800) Lh(1) Mt(r2) Mb(r1)'>\n      <a href=\"#extend-simple-with-creative-thinking\" class='Cur(d)!'>Extend Simple with Creative Thinking</a>\n    </h2><p class='Mb(r1)'>Jiro isn&#39;t making the same sushi now as he was 50 years ago. Even though he learned from great sushi chefs that said there was nothing left they could learn, he didn&#39;t believe it and he kept pushing to improve. He does things that weren&#39;t or sometimes still aren&#39;t the norm in the sushi industry. One example that struck me, was he even adjusted the size of each dish he gave to individual customers to ensure they would all finish at the same time.</p><p class='Mb(r1)'>Once we have our standards on the web, we sometimes take them as fact and don&#39;t attempt to question or better them over time. <a href='https://medium.com/designing-medium/7c03a9274f9'\n    \n    class='Link'>Why do we use text-decoration: underline</a>? <a href='http://alistapart.com/article/responsive-web-design/'\n    \n    class='Link'>Why are our designs 960px wide</a>? If we didn&#39;t question best practices from time to time, we wouldn&#39;t progress the web.</p><p class='Mb(r1)'>A recent example is, Mikkel Bo Schmidt&#39;s <a href='https://medium.com/design-ux/bc190d62eff5'\n    \n    class='Link'>re-design of the select element</a>. Rather than accept a default select element was good enough for a device agnostic site (anyone that uses iOS will know that it isn&#39;t), Mikkel&#39;s company opted to design something specific and more user friendly to their specific problem.</p><p class='Mb(r1)'>Now, I am not saying we should spend a month on each individual detail of our designs, but each time we can invest a little extra time and passion into an important part of our craft, that is experience and quality that we now have for our next project. Doing a little of this at every chance we get, is what helps us become craftspeople.</p>"}

